body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/************************************************************************************/
/**** PAGE ****/
.containerPage {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  min-height: 780px;
}

/**** navBar ****/
.navHContainer {
  position: absolute;
  top: 0;
  margin: 0;
  display: flex;
  align-items: center;
  width: 100%;
  height: 91px;
  background-color: #000000;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  z-index: 1;
}
.logoContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16%;
  height: 100%;
}
.logo {
  width: 70%;
}
.horizontalMenu {
  width: 75%;
  height: 100%;
  margin-left: 3%;
}
.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}
.btnNavBar {
  width: auto;
  height: 35px;
  text-decoration: none;
  font-size: 1.5rem;
  color: #ffffff;
  border: 3px solid #000000;
}
@media screen and (max-width: 1024px) {
  .btnNavBar {
    font-size: 1.2rem;
  }
}
.btnNavBar:hover {
  border-bottom: 3px solid #fbfbfb;
  transition: border-bottom 0.8s;
}

/**** sideBar ****/
.verticalMenu {
  position: absolute;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 117px;
  height: 100%;
  min-height: 780px;
  background-color: #000000;
}
.containerIcon {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 250px;
}
@media screen and (max-width: 1440px) {
  .containerIcon {
    margin-top: 320px;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1439px) {
  .containerIcon {
    margin-top: 250px;
  }
}
.navIco {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.icoBtn {
  padding: 3px;
  cursor: pointer;
  border: 2px solid #000000;
}
@media screen and (max-width: 1024px) {
  .icoBtn {
    width: 50px;
    height: 50px;
  }
}
.icoBtn:hover {
  border-radius: 10px;
  border: 2px solid red;
}

.copyright {
  position: absolute;
  bottom: 5%;
  display: flex;
  align-items: center;
  color: #ffffff;
  font-size: 12px;
  width: 140px;
  height: 12%;
  transform: rotate(-90deg);
}
@media screen and (max-width: 1024px) {
  .copyright {
    font-size: 10px;
    margin-bottom: 40%;
    height: 6%;
  }
}

/**************************************************************/
/**** CONTAINER STATISTIQUES name/analyze ****/
.userContainer {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 93px;
  left: 200px;
  top: 120px;
  height: 75vh;
}
@media screen and (max-width: 1440px) {
  .userContainer {
    top: 140px;
    height: 77vh;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1439px) {
  .userContainer {
    right: 23px;
    left: 140px;
    height: 74vh;
  }
}

/**** CONTAINER NAME ****/
.nameContainer {
  width: 100%;
  height: 18%;
}
.hello {
  font-size: 48px;
  margin: 0;
}
@media screen and (max-width: 1024px) {
  .hello {
    font-size: 36px;
  }
}
.firstName {
  color: #ff0000;
}
.congratulation {
  font-size: 18px;
  margin: 0;
}
@media screen and (max-width: 1024px) {
  .congratulation {
    font-size: 16px;
    margin: 0;
  }
}
.bravo {
  width: 25px;
  height: 25px;
  margin-left: 10px;
}

/**** CONTAINER CHARTS ANALYZE .... ****/
.statsContainer {
  display: flex;
  width: 100%;
  height: 80%;
  margin-top: 20px;
}
.chartsContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 90%;
  height: 100%;
}

/*activity*/
.container-activity {
  position: relative;
  width: 95%;
  height: 50%;
}
.daily-activity {
  width: 100%;
  height: 100%;
  background-color: #fbfbfb;
  border-radius: 5px;
}
.titleDailyActivity {
  position: absolute;
  margin: 0;
  margin-left: 25px;
  margin-top: 20px;
  font-size: 15px;
  font-weight: bold;
}
.custom-tooltip {
  display: flex;
  flex-direction: column;
  width: 90px;
  height: 70px;
  color: #fff;
}
@media screen and (max-width: 1024px) {
  .custom-tooltip {
    width: 65px;
    height: 60px;
  }
}
.labelDailyActivity {
  background-color: rgb(230, 0, 0);
  margin: 0;
  height: 50%;
  text-align: center;
  margin-top: 20px;
}
@media screen and (max-width: 1024px) {
  .labelDailyActivity {
    font-size: 12px;
  }
}
.desc {
  background-color: rgb(230, 0, 0);
  margin: 0;
  height: 50%;
  text-align: center;
}
@media screen and (max-width: 1024px) {
  .desc {
    font-size: 12px;
  }
}
.recharts-cartesian-axis-line {
  display: none;
}
.recharts-cartesian-axis-tick-line {
  display: none;
}
.recharts-wrapper .recharts-cartesian-grid-horizontal line:first-child {
  stroke-dasharray: 0;
}

/*sessions global*/
.container-sessions {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  width: 95%;
  height: 40%;
}

.spider-analytics,
.objective-score,
.average-duration {
  width: 30%;
  height: 100%;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.0212249);
  border-radius: 5px;
}
.average-duration {
  position: relative;
  background-color: #ff0000;
}
.average-duration-bgc {
  position: absolute;
  top: 0;
  right: 0;
  width: 27%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 0 5px 5px 0;
}
.titleSessionTime {
  position: absolute;
  margin: 0;
  margin-top: 20px;
  margin-left: 30px;
  width: 65%;
  font-size: 15px;
  color: #fff;
  mix-blend-mode: normal;
  opacity: 0.5;
}
@media screen and (max-width: 1260px) {
  .titleSessionTime {
    font-size: 12px;
  }
}
.labelSession {
  background-color: #fff;
  width: 65%;
  color: #000000;
  height: 35px;
  line-height: 35px;
  text-align: center;
}
.spider-analytics {
  background-color: #282d30;
}
.objective-score {
  position: relative;
  background-color: #fbfbfb;
}
.titleScore {
  position: absolute;
  margin: 0;
  top: 10px;
  left: 20px;
  font-weight: bold;
}
.tauxObjectif {
  position: absolute;
  margin: 0;
  top: 35%;
  left: 35%;
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #74798c;
}
@media screen and (max-width: 1024px) {
  .tauxObjectif {
    font-size: 12px;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1439px) {
  .titleScore {
    left: 0;
    top: 40px;
    font-size: 18px;
    text-align: center;
    width: 100%;
    height: auto;
  }
}
/**** CONTAINER ENERGY ****/
.container-energy {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}
.calories,
.proteines,
.glucides,
.lipides {
  display: flex;
  width: 258px;
  height: 90px;
  background-color: #fbfbfb;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.0212249);
  border-radius: 5px;
}
@media screen and (max-width: 1024px) {
  .calories,
  .proteines,
  .glucides,
  .lipides {
    width: 258px;
    height: 124px;
  }
}
@media screen and (max-width: 1024px) {
  .calories,
  .proteines,
  .glucides,
  .lipides {
    width: 254px;
    height: 94px;
  }
}
.img-energy {
  width: 60px;
  margin-left: 35px;
}
.infos-energy {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  margin-left: 20px;
}
.number-energy {
  margin: 0;
  font-size: 20px;
  font-weight: bold;
}
.title-energy {
  margin: 0;
  font-size: 14px;
  color: #74798c;
}

.error,
.loading {
  display: flex;
  margin-right: 0;
  width: 92%;
  height: 100px;
  margin-left: 8%;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: bold;
  color: rgb(178, 178, 224);
}
.error {
  color: red;
}

/**** PAGE ACCUEIL POUR DEMO ****/
.homePage {
  height: 100vh;
  background-color: #fbfbfb;
}
.homeNav {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  top: 30px;
  margin-left: 10%;
}
.titleDemo {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 40vh;
  align-items: center;
  justify-content: flex-end;
}
.titleHome {
  font-size: 2.8rem;
  margin: 0;
}
.subTitleDemo {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.2rem;
  font-weight: bold;
  color: #74798c;
}
.logoIsolé {
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.logoImg {
  width: 300px;
  border: 1px solid red;
  border-radius: 50%;
  box-shadow: 1px 2px 4px 2px rgb(92, 76, 76);
}
.navId {
  display: flex;
  justify-content: space-between;
  width: 250px;
  height: auto;
}
.userBtn {
  text-decoration: none;
  font-size: 1.5rem;
  text-align: center;
  width: 100px;
  height: 40px;
  line-height: 40px;
  color: #fbfbfb;
  background-color: red;
  padding: 0 8px;
  border-radius: 25px;
  box-shadow: 1px 2px 2px 1px black;
}
.userBtn:hover {
  color: #74798c;
}
/******************************************************************************/
